.ProgressBar {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: #000;
  width: 100%;
  height: 3px;
  border: 0;
}

.ProgressBar::-webkit-progress-bar {
  background-color: #000;
}

.ProgressBar::-moz-progress-bar {
  background-color: #f76725;
}

.ProgressBar::-ms-fill {
  background-color: #f76725;
}

.ProgressBar::-webkit-progress-value {
  background-color: #f76725;
}



