.TopBar {
  display: -ms-flexbox;
  display: flex;
  border-bottom: 2px solid #000000;
  padding: 10px;
}

.TopBar-left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.TopBar-left > * {
  white-space: nowrap;
}
.TopBar-left > *,
.TopBar-right > * {
  margin: 0.5rem;
}

.TopBar-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.TopBar-right :nth-child(2) {
  margin-left: auto;
  white-space: nowrap;
}



