.EtherBlockBox-topbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #000000;
}

.EtherBlockBox-topbar h2 {
  font-weight: 100;
  width: 12rem;
}

.EtherBlockBox-buttons {
  display: -ms-flexbox;
  display: flex;
}

.EtherBlockBox-buttons button {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 0.5rem 1rem;
  text-align: right;
}

.EtherBlockBox-buttons button:first-child {
  border-left: none;
}

.EtherBlockBox-buttons button.active {
