      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.EtherBlockBox-arrow-prev {
  border-width: 0.1rem 0 0 0.1rem;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.Item {
  text-align: left;
  line-height: 2rem;
}
.Item-label {
  display: inline-block;
  width: 8rem;
  text-align: right;
  margin-right: 0.2rem;
}
.Item-content {
  display: inline-block;
}



